.spinnerWrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    position: fixed;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    z-index: 2;
    min-width: 52px;
    min-height: 160px;
    color: black;
    text-align: center;
  }
  .ldsRoller {
    display: inline-block;
    width: 80px;
    height: 80px;
    padding: 20px;
    top: 0%;
    z-index: 6;
    border-radius: 10px;
  }
  .ldsRoller div {
    animation: ldsRoller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    transform-origin: 40px 40px;
  }
  .ldsRoller div:after {
    content: " ";
    display: block;
    position: absolute;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background-color: black;
    margin: -4px 0 0 -4px;
  }
  .ldsRoller div:nth-child(1) {
    animation-delay: -0.036s;
  }
  .ldsRoller div:nth-child(1):after {
    top: 63px;
    left: 63px;
  }
  .ldsRoller div:nth-child(2) {
    animation-delay: -0.072s;
  }
  .ldsRoller div:nth-child(2):after {
    top: 68px;
    left: 56px;
  }
  .ldsRoller div:nth-child(3) {
    animation-delay: -0.108s;
  }
  .ldsRoller div:nth-child(3):after {
    top: 71px;
    left: 48px;
  }
  .ldsRoller div:nth-child(4) {
    animation-delay: -0.144s;
  }
  .ldsRoller div:nth-child(4):after {
    top: 72px;
    left: 40px;
  }
  .ldsRoller div:nth-child(5) {
    animation-delay: -0.18s;
  }
  .ldsRoller div:nth-child(5):after {
    top: 71px;
    left: 32px;
  }
  .ldsRoller div:nth-child(6) {
    animation-delay: -0.216s;
  }
  .ldsRoller div:nth-child(6):after {
    top: 68px;
    left: 24px;
  }
  .ldsRoller div:nth-child(7) {
    animation-delay: -0.252s;
  }
  .ldsRoller div:nth-child(7):after {
    top: 63px;
    left: 17px;
  }
  .ldsRoller div:nth-child(8) {
    animation-delay: -0.288s;
  }
  .ldsRoller div:nth-child(8):after {
    top: 56px;
    left: 12px;
  }
  @keyframes ldsRoller {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  